
/*Обнуление*/
*{
	padding: 0;
	margin: 0;
	border: 0;
}
*,*:before,*:after{
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}

nav,footer,header,aside{display: block;}

html,body, #root{
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,button,textarea{font-family:inherit;}

input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: 400;}
/*--------------------*/
* {
	font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.App {
  width: 100%;
  height: 100%;
  
}
.img {
	z-index: -1;
	position: absolute;
	width: 100%;
	height: 100%;
	object-fit: cover;
	opacity: 0.77;
}	
.background-img {
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: #131929;
	z-index: -5;
}

.header {
  height: 100px;
  font-weight:  bold;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 42px;
  border-bottom: 1px solid black;
}
.logout-btn {
	width: 50px;
	height: 50px;
}
.login-btn {
	width: 40px;
	height: 40px;
	border-radius: 30px;
	margin-right: 5px;
}


.auth {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-right: 5%;

}
.text {
	position: relative;
	color: white;
	font: 18px "Roboto";
	cursor: pointer;
	line-height: 1; 
	text-decoration: none; 
	font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.text:after{
	display: block;
	position: absolute;
	left: 0; 
	width: 0;
	height: 2px;
	background-color: #fa721e; 
	content: "";
	transition: width 0.3s ease-out; 
}

.text:hover:after,
.text:focus:after {
	width: 100%;
}
.menu {
	display: flex;
	margin-right: 20%;
	width: 30%;
	justify-content: space-between;
}
.footer {
	position: absolute;
	max-width: 100%;
	object-fit: cover;
}
.footer-image {
	width: 100%;
	object-fit: cover;
}
.modal {
	position: absolute;
	width: 637px;
	height: 375px;
	left: 50%;
  	top: 50%;
	transform: translate(-50%,-50%);
	
}
.modal-background {
	position: absolute;
	background-color: #0B2740;
	opacity: 0.7;
	width: 100%;
	height: 100%;
	border: 5px solid #1B3B6F;
	transform: matrix(1, 0, 0, -1, 0, 0);
	border-radius: 45px;
	z-index: -1;
	
}
.modal-content {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	height: 100%;
	position: absolute;
	
}
.modal-text {
	width: 569px;
	font: bold 24px "Roboto";
	text-align: center;
	color: white;
	line-height: 28px;
	font-weight: 900;
}
.trade-btn {
