.payment-list {
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    padding-bottom: 19px;
    border-bottom: 1px solid #2e3238;
	padding-left: 20px;
	padding-right: 20px;
}

.payment-list div {
    cursor: pointer;
    width: calc(100% - 10px);
    height: 65px;
    display: inline-block;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: 69px;
    border: 1px solid rgba(255,255,255,.1);
    border-radius: 4px;
    margin-bottom: 7px;
    transition: all .2s;
}

